import React, { useState, useEffect } from 'react';

import moment from 'moment';
import 'moment/locale/ko';	//대한민국

function Time(props) {
  
  const [momentDate, setMomentDate] = useState(moment().format('YYYY.MM.DD HH:mm:ss'));
  
  console.log("렌더링이 됩니다..?") //렌더링이 잘 되는지 확인용! 꼭 넣고 진행해주세요.

  useEffect(() => {
    let time = setInterval(() => {
      setMomentDate(moment().format('YYYY.MM.DD HH:mm:ss'));
    }, 1000);
    return () => {
  	    // 컴포넌트가 사라지기 전에 setinterval을 clearinterval해줍니다
        clearInterval(time);
    };
  } );

  return (
    <div className="noCss"> 
        {momentDate}
    </div>
  );
}

export default Time;